''
import { useState } from 'react';
import { HomeIcon, CameraIcon } from '@heroicons/react/solid';  

const Sidebar = () => {
  const [isCameraOpen, setIsCameraOpen] = useState(false);
  const [activeMenu, setActiveMenu] = useState(null);

  const toggleCamera = () => {
    setIsCameraOpen(!isCameraOpen);
  };

  const setActive = (menu: any) => {
    setActiveMenu(menu);
  };

  return (
    <div className="w-64 h-screen bg-[#21262D] text-white">
      <div className="p-5 text-xl font-bold">Menu</div>
      <ul className="space-y-4">
        {/* Mục Trang chủ */}
        <li 
          className={`flex items-center space-x-3 p-3 rounded cursor-pointer ${activeMenu === 'home' ? 'bg-[#3B8FEE]' : 'hover:bg-gray-700'}`} 
          onClick={() => setActive('home')}
        >
          <HomeIcon className="w-5 h-5" />
          <a href="#">Trang chủ</a>
        </li>
        
        {/* Mục Camera, hiển thị danh sách khi click */}
        <li 
          className={`flex items-center space-x-3 p-3 rounded cursor-pointer ${activeMenu === 'camera' ? 'bg-[#3B8FEE]' : 'hover:bg-gray-700'}`}
          onClick={() => { toggleCamera(); setActive('camera'); }}
        >
          <CameraIcon className="w-5 h-5" />
          <a href="#">Camera</a>
        </li>

        {/* Danh sách Camera (hiển thị khi click vào Camera) */}
        {isCameraOpen && (
          <ul className="ml-4 space-y-2 mt-2">
            <li className="hover:bg-gray-600 p-2 rounded">
              <a href="#">Cam 1</a>
            </li>
            <li className="hover:bg-gray-600 p-2 rounded">
              <a href="#">Cam 2</a>
            </li>
            <li className="hover:bg-gray-600 p-2 rounded">
              <a href="#">Cam 3</a>
            </li>
            <li className="hover:bg-gray-600 p-2 rounded">
              <a href="#">Cam 4</a>
            </li>
          </ul>
        )}
      </ul>
    </div>
  );
}

export default Sidebar;